import React, { useState } from 'react';

const Search = () => {
  const [query, setQuery] = useState("");

  return (
    <div>
      <input 
        type="text"
        placeholder="Search"
        value={query} 
        onChange={(e) => setQuery(e.target.value)} // Handle input change
      />
    </div>
  );
};

export default Search;
